<!-- UserLogin.vue -->
<template>
  <div class="bg">
    <div class="container d-flex justify-content-center align-items-center vh-100">
      <div class="card p-4">
        <h2 class="text-center">Login</h2>
        <form @submit.prevent="login">
          <div class="form-group">
            <label for="email">Email address</label>
            <input type="email" class="form-control" v-model="email" placeholder="Enter email" required>
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <input type="password" class="form-control" v-model="password" placeholder="Password" required>
          </div>
          <button type="submit" class="btn btn-primary btn-block">Login</button>
          <p class="text-center mt-3">Don't have an account? <router-link to="/register">Register</router-link></p>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UserLogin',
  data() {
    return {
      email: '',
      password: '',
      isAuthenticated: false
    };
  },
  methods: {
    login() {
      const user = JSON.parse(localStorage.getItem('user'));
      if (user && user.email === this.email && user.password === this.password) {
        this.$store.dispatch('login', user);
        this.$router.push('/members');
      } else {
        alert('Invalid email or password');
      }
    }
  }
};
</script>

<style scoped>
.bg {
  background-image: url('@/assets/home.jpg');
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.container {
  height: 100vh;
}
</style>
