<!-- UserRegister.vue -->
<template>
  <div class="bg">
    <div class="container d-flex justify-content-center align-items-center vh-100">
      <div class="card p-4">
        <h2 class="text-center">Register</h2>
        <form @submit.prevent="register">
          <div class="form-group">
            <label for="name">Full Name</label>
            <input type="text" class="form-control" v-model="name" placeholder="Enter full name" required>
          </div>
          <div class="form-group">
            <label for="email">Email address</label>
            <input type="email" class="form-control" v-model="email" placeholder="Enter email" required>
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <input type="password" class="form-control" v-model="password" placeholder="Password" required>
          </div>
          <button type="submit" class="btn btn-primary btn-block">Register</button>
          <p class="text-center mt-3">Already have an account? <router-link to="/login">Login</router-link></p>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UserRegister',
  data() {
    return {
      name: '',
      email: '',
      password: ''
    };
  },
  methods: {
    register() {
      const user = {
        name: this.name,
        email: this.email,
        password: this.password
      };
      localStorage.setItem('user', JSON.stringify(user));
      this.$router.push('/login');
    }
  }
};
</script>

<style scoped>
.bg {
  background-image: url('@/assets/home.jpg');
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.container {
  height: 100vh;
}
</style>
